<template>
  <!-- 购物车 -->
  <div class="g_car">
    <div class="g_car-cn auto-app">
      <div class="car-l">
        <ul class="car-list">
          <li>商品</li>
          <li>价格</li>
          <li>数量</li>
          <li>合计</li>
        </ul>
        <!-- 商品列表 -->
        <ul class="sp_list">
          <li  v-for="(i,$index) in this.$store.state.nlist" :key="$index">
            <i class="iconfont icon" @click="go1($index)">&#xe68a;</i>
            <ul class="mx_list">
              <li>
                <div class="mx-img">
                  <img :src="i.img6" alt="" />
                </div>
                <p class="xm-name">{{i.names1}}</p>
              </li>
              <li>
                <p class="xm-my">￥{{i.myy}}</p>
              </li>
              <li>
                <button class="bbt" @click='maijia($index)'>+</button>
                <div class="mx-sch">
                  <input type="text" :value="i.nub">
                  <!-- <span>{{i.nub}}</span> -->
                </div>
                <button class="bbt" @click="maijian($index)">-</button>
              </li>
              <li>
                <p class="zj">￥{{i.myy*i.nub}}</p>
              </li>
            </ul>
          </li>
        </ul>
      </div>
      <div class="car-r">
        <h3 class="car-r-top">购物车总计</h3>
        <ul class="my-s">
          <li>
            <div class="my-ty">
              <p class="my-nm">购物车小计</p>
              <p class="my-y">￥{{hj}}</p>
            </div>
            <div class="my-bt">
              <p class="my-nm">运费</p>
              <p class="my-y">免费送货</p>
            </div>
          </li>
          <li>
            <div class="my-btn">
              <p class="my-nm">订单总计</p>
              <p class="my-y">￥{{hj}}</p>
            </div>
          </li>
        </ul>
        <div class="car-r-cn">
          <div class="car-link">
            <span>前往收银台</span>
          </div>
          <p class="yhq">优惠券：</p>
          <div class="m-sch">
            <div class="u-sch">
              <input type="text" placeholder="优惠券代码" />
            </div>
            <div class="car-link">
              <span>使用优惠券</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapActions } from "vuex";
export default {
    data() {
      return {
      }
    },
    methods: {
    ...mapActions(["gw2",'go1']),
    maijia(index){
      this.$store.state.nlist[index].nub>=10?10:this.$store.state.nlist[index].nub++
    },
     maijian(index){
       this.$store.state.nlist[index].nub<=1?1:this.$store.state.nlist[index].nub--
    }
  },
  computed:{
    hj(){
      let heji=0;
      for(let i =0;i<this.$store.state.nlist.length;i++){
          heji+=this.$store.state.nlist[i].nub*this.$store.state.nlist[i].myy
      } 
      return heji;  
    }
  }
};
</script>
<style>
