<template>
  <div class="tealists">
    <div class="tealist-hd">
      <!-- 头部 -->
      <h3 class="lf">茶叶</h3>
      <ul class="tea-list-hds">
        <li v-for="(i, $index) in b_arr" :key="$index" @click="btn(i.type)">
          {{ i.type }}
        </li>
      </ul>
    </div>
    <ul class="tea-img clearfix">
      <li
        v-for="(i, $index) in arr"
        :key="$index"
        class="animate__animated animate__fadeInUp animate__delay-.5s" 
      >
       <router-link :to="'/so/' + i.id" tag="div">
          <div class="tea-imgs">
          <img :src="i.img6" alt="" />
        </div>
        <div class="tea-cn">
          <p class="u-title1">{{ i.names1 }}</p>
          <p class="u-my">￥{{ i.myy }}</p>
          <div class="link">
            <span class="iconfont">&#xe639;</span>
          </div>
        </div>
       </router-link>
      </li>
    </ul>
    <div class="txt" v-if="listarr.length>10">
      <div @click="index <= 0 ? 0 : index--">上一页</div>
      <div
        v-for="(i, $index) in page"
        :class="index == $index ? 'active' : ''"
        @click="index = $index"
        :key="$index"
      >
        {{ i }}
      </div>
      <div @click="index >= page - 1 ? page - 1 : index++">下一页</div>
      <div @click="index = page - 1">最后一页</div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  mounted() {
    document.body.scrollTop = 0;
    document.documentElement.scrollTop = 0;
  },
  data() {
    return {
      index: 0,
      num: 10,
      arr:[]
    };
  },
  methods:{
       btn(c){
        this.$store.state.names = c;
      },
  },
  created(){
    if(this.listarr.length>10){
      this.arr = this.listarr.slice(this.index*10,(this.index+1)*10)
    }
    else{
      this.arr = this.listarr
    }
  },
  computed: {
    page() {
      let t = Math.ceil(this.$store.state.tealist.length / this.num);
      return t;
    },
    setgo() {
      let p = this.$store.state.tealist.slice(
        this.index * this.num,
        this.index * this.num + this.num
      );
      return p;
    },
    ...mapGetters(["b_arr", "listarr"]),
  },
  watch:{
      index(){
        if(this.listarr.length>10){
          this.arr = this.listarr.slice(this.index*10,(this.index+1)*10)
        }
        else{
          this.arr = this.listarr
        }
    },
      listarr(){
         if(this.listarr.length>10){
            this.arr = this.listarr.slice(this.index*10,(this.index+1)*10)
          }
          else{
            this.arr = this.listarr
          }
      }
  }
};
</script>
<style scoped>
.clearfix::after,
.clearfix::before {
  content: "";
  display: table;
}
  img{
    width:100%;
    height:100%;
    vertical-align:top;
  }
.clearfix::after {
  clear: both;
}
.tea-img {
  padding: 40px;
}
.tea-img li {
  float: left;
  width: 20%;
  margin-bottom: 10px;
  padding: 10px;
  box-sizing: border-box;
}
.tea-imgs {
  overflow: hidden;
  margin-bottom: 10px;
}
.tea-img li:hover img {
  transform: scale(1.1);
  transition: all 0.5s ease;
}
.tea_type {
  background-color: #f7f7f7;
  padding-top: 100px;
  padding-bottom: 100px;
}
.u-title1 {
  color: #555555;
  font-size: 16px;
  line-height: 22px;
  padding-bottom: 20px;
  position: relative;
  margin-bottom: 10px;
}
.u-title1::after {
  position: absolute;
  content: "";
  width: 0;
  height: 1px;
  background-color: #519f10;
  left: 0px;
  bottom: -1px;
  margin: 0;
  transition: all 0.6s ease 0s;
}
.tea-img li:hover .u-title1::after {
  transition: all 0.5s ease;
  animation-delay: 3s;
  width: 100%;
}
.tea-img li:hover .u-title1 {
  color: #5ba41e;
}
.u-my {
  color: #555555;
  font-size: 16px;
  font-family: sans-serif;
  margin-bottom: 10px;
}
.link {
  width: 100%;
  height: 46px;
}
.tea-img li:hover .link span {
  border-color: #5ba41e;
  color: #5ba41e;
}
.link span {
  font-size: 12px;
  padding: 0 10px;
  border: 1px solid #d8d8d8;
  color: #d8d8d8;
  text-decoration: none;
  height: 20px;
  line-height: 20px;
  display: inline-block;
}
.tea-img li {
  animation-delay: 0.5s;
}
.tea-img li:nth-child(1) {
  animation-delay: 0.3s;
}
.tea-img li:nth-child(2) {
  animation-delay: 0.4s;
}
.tea-img li:nth-child(3) {
  animation-delay: 0.5s;
}
.tea-img li:nth-child(4) {
  animation-delay: 0.6s;
}
.tea-img li:nth-child(5) {
  animation-delay: 0.7s;
}
.tea-img li:nth-child(6) {
  animation-delay: 0.8s;
}
.tea-img li:nth-child(7) {
  animation-delay: 0.9s;
}
.tea-img li:nth-child(8) {
  animation-delay: 1s;
}
.tea-img li:nth-child(9) {
  animation-delay: 1.1s;
}
.tea-img li:nth-child(10) {
  animation-delay: 1.2s;
}
.tea-img li:nth-child(11) {
  animation-delay: 1.3s;
}
.tea-img li:nth-child(12) {
  animation-delay: 1.4s;
}
.tea-img li:nth-child(13) {
  animation-delay: 1.5s;
}
.tealist-hd {
  display: flex;
  height: 39px;
  padding: 20px 40px;
  background-color: #f9f9f9;
  justify-content: space-between;
}
.tea-list-hds {
  display: flex;
  line-height: 39px;
}
.tea-list-hds li {
  padding: 0 20px;
  color: #878787;
  cursor: pointer;
  font-size: 15px;
  position: relative;
}
.tea-list-hds li::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 1px;
  background-color: #90af6b;
  left: 0;
  bottom: 0;
  display: none;
}
.tea-list-hds li:hover::after {
  display: block;
}
.txt {
  display: flex;
  justify-content: center;
  margin-bottom: 40px;
}
.txt div {
  padding: 5px 10px;
  border: 1px solid black;
  border-radius: 5px;
  margin: 5px;
}
.active {
  background-color: #eaeaea;
  color: #90af6b;
}
</style>
