let getters={
    go(state){
        state.nlist=[...state.newslist.splice(0,2)]
        console.log(state.nlist)
        return state.nlist
    },
    b_arr(state){
        let lp = [];
        for(var i = 0; i < state.tealist.length;i++){
            if(lp.indexOf(state.tealist[i].t)== -1){
                lp.push(state.tealist[i].t);
            }
        }

        let newlist = [{type:'全部',num: state.tealist.length}];
        for(var c = 0; c< lp.length;c++){
            let num=0;
            for(var a = 0; a< state.tealist.length;a++){
                if(lp[c] ==  state.tealist[a].t){
                    num+=1;
                }
            }
            let json = {type:lp[c],num:num}
            newlist.push(json)
        }
        return newlist
    },
    listarr(state){
        let listarr = []
        for (let i = 0; i < state.tealist.length; i++) {
            if(state.names == '全部'){
                listarr.push(state.tealist[i])
            }
            else if(state.names == state.tealist[i].t){
                listarr.push(state.tealist[i])
            }
            
        }
        return listarr
    },
}
let mutations = {
    gw1(state,num){
        let type =true;
        for(var i =0;i<state.nlist.length;i++){
            if( num.id==state.nlist[i].id){
                state.nlist[i].nub+=1
                type = false
            }
        }
        if(type){
            state.nlist.push(num)    
        }
        localStorage.setItem('str',JSON.stringify(state.nlist))
    },

    gbb(state,index){
        state.nlist.splice(index,1)
    }
}
let actions = {
    go1({commit},index){
        commit('gbb',index)

    },
    gw2({commit},num){
        commit('gw1',num)
        localStorage.setItem('str',JSON.stringify(state.nlist))
    }
}
export default new Vuex.Store({
    state,
    getters,
    mutations,
    actions,
})