<script>
import fdj from "../components/fdj.vue";
import { mapActions } from "vuex";
export default {
  components: {
    fdj,
  },
  data() {
    return {
      t: this.$route.params.id,
    };
  },
  mounted() {
    document.body.scrollTop = 0;
    document.documentElement.scrollTop = 0;
    // console.log(this.$route.params.id);
    // console.log(this.$store.state.tealist)
  },
  computed: {
    ww() {
      let y = [];
      for (let i = 0; i < this.$store.state.tealist.length; i++) {
        if (this.$route.params.id == this.$store.state.tealist[i].id) {
          y.push(this.$store.state.tealist[i]);
        }
      }
      return y;
    },
    gg() {
      let p = [];
      for (let i = 0; i < this.$store.state.tealist.length; i++) {
        if (
          this.$store.state.tealist[i].t == this.ww[0].t &&
          this.$store.state.tealist[i].id != this.t
        ) {
          p.push(this.$store.state.tealist[i]);
        }
      }
      return p;
    },
  },
  watch: {
    "$route.params.id"() {
      this.t = this.$route.params.id;
    },
  },
   methods: {
    ...mapActions(["gw2",'go1']),
    jia(index){
     this.ww[index].nub>=10?10: this.ww[index].nub++
    },
     jian(index){
      this.ww[index].nub<1?1: this.ww[index].nub--
    }
  },
};
</script>
