import nav1 from './components/nav1.vue'
import banner from './components/banner.vue'
import tea1 from './components/tea1.vue'
import lists from './components/lists.vue'
import footers from './components/footers.vue'
import slots from './components/slots.vue'
import slots1 from './components/slots1.vue'
import fdj from './components/fdj.vue'

let publics ={
    install(Vue){
        Vue.component('nav1',nav1)
        Vue.component('banner',banner)
        Vue.component('tea1',tea1)
        Vue.component('lists',lists)
        Vue.component('footers',footers)
        Vue.component('slots',slots)
        Vue.component('slots1',slots1)
        Vue.component('fdj',fdj)
    }
}

export default publics