import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import About from '../views/About.vue'
import tea from '../views/tea.vue'
import jd from '../views/jd.vue'
import news from '../views/news.vue'
import tl from '../views/tl.vue'
import news1 from '../views/news1.vue'
import news2 from '../views/news2.vue'
import news3 from '../views/news3.vue'
import so from '../views/so.vue'
import gw from '../views/gw.vue'

Vue.use(VueRouter)

const routes = [
    { path:'/',redirect:'Home'},
    {path:'/Home',name:'Home',component:Home},
    {path:'/About',name:'About',component:About},
    {path:'/tea',name:'tea',component:tea},
    {path:'/jd',name:'jd',component:jd},
    {path:'/news',name:'news',component:news,children:[
      { path:'/',redirect:'news1'},
      { path:'news1',name:'new1',component:news1},
      { path:'news2',name:'new2',component:news2},
      { path:'news3',name:'new3',component:news3},
    ]},
    {path:'/tl',name:'tl',component:tl},
    {path:'/so/:id',name:'so',component:so},
    {path:'/gw',name:'gw',component:gw},
]

const router = new VueRouter({
  // mode: 'history',

  routes
})

export default router
